import React from "react";
import {HanderWidget} from "./widget/hander";
import styled from "styled-components";
import {MessageWidget} from "./widget/message";
import {MyNftWidget} from "./widget/my_nft";
import {useGetPoolMessage} from "../main/hook/useGetPoolMessages";
import {CountdownWidget} from "../main/widget/countdown_widget";

export const MintPage:React.FC = () => {
    const {date, nftStake, nftTotal} = useGetPoolMessage({nftAddress: "address"})

    return (
        <MintStyle>
            <HanderWidget  endTime={parseInt(date[1])} startTime={parseInt(date[0])}  />
            <div className="box">
                <MessageWidget  date={date} nftStake={nftStake} nftTotal={nftTotal} />
                <MyNftWidget />
            </div>
        </MintStyle>
    )
}


const MintStyle = styled.div`
    
    .box{
      display: flex;
      padding: 20px;
      box-sizing: border-box;
      justify-content: space-around;
    }
`
