import React, {useEffect, useMemo, useState} from "react";
import {Grid} from "@material-ui/core";
import styled from "styled-components";

import {CellCollectionsWidget} from "./cell_collections";
import {CryptoWidget} from "./crypto_simeji";
import {HttpStatus, useWait} from "../../../utils/wait_fn";
import {HttpServer} from "../../../net/http_server";
import {LoadingWidget} from "../../../widget/base/loading_widget";
import {CellCollectionsData} from "../type";
import {slice} from "ramda";
import {useStore} from "effector-react";
import {$getCollections} from "../store";
import useRequestEffect from "../../../hook/useRequestEffect";
import {getCollections} from "../store/effects";
import {useCollections} from "../../../provider/collections_provicer";

function createData(
    name: string,
    img: string,
    floorPrice: number,
    bestOffer: number,
    d: number,
    volD: number,
    listing: number,
    address: string,
    refData?: any,
): CellCollectionsData {
    return {name, img, floorPrice, bestOffer, d, volD, listing, refData, address};
}


export const RightWidget: React.FC = () => {

    const data = useStore($getCollections)
    const rows: CellCollectionsData[] = useMemo(() => {
        if (!data || !data.collections) return [];
        return data.collections.filter((ev: any) => ev.name).map((ev: any) => {
            let {name, images, sell_quote, pool_count, item_count, analytics, address} = ev;
            return createData(name, images.image_url, sell_quote, pool_count, analytics.volume_24_hour, analytics.volume_all_time, item_count, address, ev);
        })
    }, [data])

    const [cryptoSimeji, setCryptoSimeji] = useState<CellCollectionsData | undefined>();

    const {collections} = useCollections()
    return (
        <RightStyle>
            <Grid
                container
                // justify={"space-around"}
                spacing={1}
            >

                {
                    rows.map(
                        (
                            (ev: any, index) => {
                                return (
                                    <Grid className={'cell_wap'} item key={`${ev.address}_${index}`}>
                                        <div onClick={() => {
                                            setCryptoSimeji(ev)
                                        }}>
                                            <CellCollectionsWidget
                                                collection={collections[ev.address]}
                                                {...ev}
                                                refD={ev.refData}
                                                index={index + 1}
                                            />
                                        </div>
                                    </Grid>
                                );
                            }
                        )
                    )
                }
            </Grid>

        </RightStyle>
    );
}


const RightStyle = styled.div`

  //max-width: 1000px;
  margin: 0 auto;

  .wap {
    background-size: 100% 100%;
  }

  .cell_wap {
    width: 32%;
  }

  .inner {
    padding: 24px;
    box-sizing: border-box;
  }


  .input {
    margin-top: 20px;
    box-sizing: border-box;


  }

  .balance {
    color: #7B7A89;
    width: 200px;

    span {
      color: #7B7A89;
    }
  }

  @media (max-width: ${({theme}) => theme.sm}) {
    .input {
      margin-top: 20px;
    }


    .charged {
      margin-bottom: 20px;
    }

    .balance {
      width: 220px;
    }
  }
`
