import React, {useState} from "react";
import styled from "styled-components";
import {DepositWidget} from "../right_widget/deposit_widget";
import {TableType} from "../type";
import {WithdrawWidget} from "../right_widget/withdraw_widget";

export const MyNftWidget: React.FC = () => {

    const [table, setTable] = useState<TableType>(TableType.Deposit)

    return (<MyNftStyle>
        <div className="table">
            <p className={table === TableType.Deposit ? 'ac' : ''} onClick={() => {
                setTable(TableType.Deposit)
            }}>
                Deposit
            </p>
            <p className={table === TableType.Withdraw ? 'ac' : ''} onClick={() => {
                setTable(TableType.Withdraw)
            }}>
                Withdraw
            </p>
        </div>

        <div className="my_nft_box">
            {
                table === TableType.Deposit && <DepositWidget/>
            }

            {
                table === TableType.Withdraw && <WithdrawWidget/>
            }
        </div>

    </MyNftStyle>)
}


const MyNftStyle = styled.div`



  .table {
    display: flex;

    justify-content: space-between;
    box-sizing: border-box;

    p {
      padding: 10px 0;
      font-family: 'ZX';
      cursor: pointer;
      font-style: normal;
      font-weight: 400;
      flex: 1;
      text-align: center;
      font-size: 20px;
      line-height: 20px;
      letter-spacing: -0.14em;
      color: #FFFFFF;
      border-bottom: 1px solid #64616C;
    }

    .ac {
      border-bottom: 1px solid #00FFB3;

    }
  }

  width: 42%;
  display: block;
  background: #191B1F;
  border: 1px solid #64616C;
  border-radius: 20px;
  box-sizing: border-box;
`
