import React, {useContext, useMemo, useState} from "react";
import {ModalContext} from "../../../provider/model_provider";
import styled from "styled-components";
import {SelectNftWidget} from "../select_nft_wigdte";
import {Grid} from "@material-ui/core";
import {MoralisData} from "../../../model/moralis_data";
import Roy from "../../../assets/images/question-mark.svg";
import {NftCardItem} from "../../inventory/widget/nft_card_item";
import {useCreatePairETH} from "../../../server/factory";
import {useSendModal} from "../../../widget/modal/send_modal";
import {useDeposit} from "../../../server/stake";
import {LoadingBtn} from "../../../compones/loading_btn";
import {PoolType} from "../../collection/pool/nft_pool_item";
import {ApproveButton} from "../../../widget/base/approve_button";
import * as R from "ramda";

export const DepositWidget: React.FC = () => {
    const [result, setResult] = useState<any[]>([]);

    const {onPresent} = useContext(ModalContext);

    const handleDeposit = useDeposit()
    const {onSend, status,} = useSendModal(handleDeposit)


    let group: any = useMemo(() => {
        if (!result?.length) {
            return {}
        }
        let group = R.compose(
            R.groupBy((student: MoralisData) => {
                return student.token_address;
            }),
        )(result)

        return group;
    }, [result])

    return (
        <DepositStyle>
            <div className="btn" onClick={() => {
                onPresent(<SelectNftWidget ids={
                    result.map((ev: any) => ev.token_address + '_' + ev.token_id)
                } addNfts={(ids: any[]) => {
                    setResult([...ids])
                }}/>)
            }}>Select NFT
            </div>
            <div className="nft_box">
                <h3 className="total_nfts">Total Deposit NFTs</h3>
                <div className="nfts">
                    <Grid className={'nft_wap'} container spacing={2}>
                        {
                            (result || []).map((ev: MoralisData) => {
                                let metadata = JSON.parse(ev.metadata)
                                if (!metadata) {
                                    metadata = {
                                        name: ev.name,
                                        image: Roy
                                    }
                                }
                                return <Grid item key={`${ev.name}_${ev.token_id}`}>
                                    <NftCardItem
                                        data={{
                                            id: ev.token_address,
                                            imgUrl: metadata.image || metadata.image_url,
                                            nftId: ev.token_id,
                                            symbol: metadata.name || ev.name,
                                            isSelect: ev.isChecked,
                                            isSell: true,
                                        }}
                                        onCb={(select: boolean) => {
                                            ev.isChecked = select;
                                        }}
                                    />
                                </Grid>
                            })
                        }
                    </Grid>
                </div>
            </div>
            <h3 className="tis">
                You will deposit <span>{result.length}</span> NFTs <br/>
                for staking <span>0</span> days <br/>
                to mint <span>{result.length}</span> ROOTNFT
            </h3>
            <LoadingBtn isLoading={status} onClick={() => {
                let key = Object.keys(group)[0]
                onSend({
                    nftCtx:key,
                    nftIds: group[key]?.map((ev: MoralisData) => ev.token_id),
                })
            }}>
                Confirm
            </LoadingBtn>
            {/*<div className="btn">Confirm</div>*/}
        </DepositStyle>
    )
}


const DepositStyle = styled.div`
  padding: 20px;
  box-sizing: border-box;

  .tis {
    text-align: center;
    font-family: 'ZX';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #BDBDBD;
    span{
      color: #4FCFA9;
    }
    margin-bottom: 10px;
  }
  
  .nft_box {
    margin: 20px 0;

    .total_nfts {
      font-family: 'ZX';
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 18px;
      display: flex;
      align-items: center;
      letter-spacing: -0.14em;
      color: #FFFFFF;
      margin-bottom: 10px;
    }

    .nfts {
      height: 250px;
      overflow-y: scroll;
      background: #191B1F;
      border: 1px solid #64616C;
      padding: 20px;
      box-sizing: border-box;
      border-radius: 10px;
    }
  }

  .btn {
    background: #52565F;
    border: 1px solid #64616C;
    border-radius: 12px;
    cursor: pointer;
    display: block;
    width: 100%;
    padding: 10px 0;
    box-sizing: border-box;
    text-align: center;
    color: white;
  }

`
