import {useCallback, useEffect, useMemo, useState} from "react";
import {HttpServer} from "../../../net/http_server";
import {mapObjIndexed} from "ramda";
import * as R from "ramda";
import {NftWithdraw} from "../type";
import {Contract} from "web3-eth-contract";
import erc721 from "../../../web3/abi/erc721";
import {useWeb3} from "../../../provider/web3_provider";
import {useWallet} from "use-wallet";
import Roy from "../../../assets/images/question-mark.svg";


export const useGetNfts = (contractAddress: string,) => {
    const [nftMeta, setNftMeta] = useState<NftWithdraw>({});
    const {contrcts, web3, block} = useWeb3()
    const {account} = useWallet()


    const handeGetMateData = useCallback(async (map: NftWithdraw) => {
        mapObjIndexed(async (value, key) => {
            if (value.imgUrl) {
                return;
            }
            let _meta: any = await HttpServer.alchemyapi(contractAddress, value.nftId || '0')
            let image = _meta?.metadata?.image
            if (!image) {
                image = Roy
            }
            let _data = {
                [value.nftId]: {
                    ...value,
                    imgUrl: image
                }
            }
            map[key].imgUrl = image;
            setNftMeta(R.mergeDeepRight(map, _data))
        }, map)
    }, [nftMeta])

    const handleGetPairs = useCallback(async () => {
        let erc721Contract = new web3.eth.Contract(erc721, contractAddress)
        let ids = await contrcts.Stake.methods['stakeTokenIds'](contractAddress, account).call()
        console.log(ids)
        // let ids = ["3"];
        let _nftWithdraw: NftWithdraw = {}
        let symbol = await erc721Contract.methods['symbol']().call();
        ids.forEach((ev: string) => {
            _nftWithdraw[ev] = {
                ...nftMeta[ev],
                nftId: ev,
                symbol: symbol,
                isSelect: false,
            }
        })
        setNftMeta(_nftWithdraw)
        handeGetMateData(_nftWithdraw)

    }, [account, nftMeta])

    useEffect(() => {
        handleGetPairs()
    }, [account, block])


    return {nftMeta}

}
