import React from "react";
import styled from "styled-components";
import Countdown from "react-countdown";

type CountdownProps = {
    startTime: string,
    endTime: string
}
export const CountdownWidget: React.FC<CountdownProps> = ({endTime, startTime}) => {

    let acDate = new Date().valueOf() / 1000
    if (!endTime) {
        return <CountdownStyle>
            <p>Time Remaining</p>
            <p>---</p>
        </CountdownStyle>
    }

    if (parseInt(startTime) > acDate) {
        return <CountdownStyle>
            <p>Time Starting</p>
            <p><Countdown renderer={renderer} date={new Date(parseInt(startTime) * 1000)}/></p>
        </CountdownStyle>
    }

    return (<CountdownStyle>
        <p>Time Remaining</p>
        <p><Countdown renderer={renderer} date={new Date(parseInt(endTime) * 1000)}/></p>
    </CountdownStyle>)
}

const renderer = (data: any) => {
    let {hours, minutes, seconds, completed, days} = data
    if (completed) {
        return <p>END</p>;
    } else {
        // Render a countdown
        return <div className={'countdown'}>
            <h3>
                <p>{days}D:</p>
            </h3>
            <h3>
                <p>{hours}H:</p>
            </h3>
            <h3>
                <p>{minutes}M:</p>
            </h3>
            <h3>
                <p>{seconds}S</p>
            </h3>
        </div>;
    }
};


const CountdownStyle = styled.div`
  padding: 14px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #4B4F59;

  p {
    font-family: 'ZX';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    display: flex;
    align-items: center;
    letter-spacing: -0.14em;
    color: #D9D9D9;
  }

  .countdown {
    display: flex;
  }
`
