import React, {useMemo} from "react";
import styled from "styled-components";
import indexAccout from "../../../assets/images/index_accout.png"
import indexEth from "../../../assets/images/index_eth.png"
import {CellCollectionsData} from "../type";
import {getBalanceFormat} from "../../../utils/formatBalance";
import {CollectionInterface} from "../../../provider/collections_provicer";
import {useGetList} from "../../collection/hook/useGetList";
import {useGetFoorPrice} from "../hook/useGetFoorPrice";
import {NftMateMap} from "../../collection/type";
import {useHistory} from "react-router-dom";
import {useGetPoolMessage} from "../hook/useGetPoolMessages";
import {reduce} from "ramda";
import BigNumber from "bignumber.js";
import Countdown from "react-countdown";
import {CountdownWidget} from "./countdown_widget";
import {ScheduleWidget} from "./schedule_widget";

type CellCollectionsProps = {
    index: number,
    collection: CollectionInterface,
    refD?: any,
} & CellCollectionsData
export const CellCollectionsWidget: React.FC<CellCollectionsProps> = ({
                                                                          name,
                                                                          img,
                                                                          index,
                                                                          address,
                                                                          collection,
                                                                          refD
                                                                      }) => {

    const {push} = useHistory()
    const {date, nftStake, nftTotal} = useGetPoolMessage({nftAddress: address})

    const totalStake = useMemo(() => {
        let listData: string[] = Object.values(nftStake);
        if (listData.length === 0) return "---"
        return reduce((acc, per: string) => acc.plus(per), new BigNumber(0))(listData).toFixed(0)
    }, [nftStake])

    const totalNft = useMemo(() => {
        let listData: string[] = Object.values(nftTotal);
        if (listData.length === 0) return "---"
        return reduce((acc, per: string) => acc.plus(per), new BigNumber(0))(listData).toFixed(0)
    }, [nftTotal])



    return (
        <CellCollections onClick={() => {
            push("/mint/" + address)
        }}>
            <div className="top">
                <div>
                    <img className={'account'} src={img} alt=""/>
                    <div className="center_box full">
                        <h4>{name}</h4>

                        <h5>
                            {refD.symbol}
                        </h5>
                    </div>
                </div>

            </div>

            <div className="bar">
                <p>STAKE TO MINT</p>
                <p>LIVE</p>
            </div>
            <div className="schedule">
                <h4>
                    <p>Amount For Mint</p>
                    <p>4000 ROOT-NFT</p>
                </h4>

                <ScheduleWidget amount={totalNft} stake={totalStake} />
            </div>
            <div className="bottom">
                <h4>
                    <p>Stake Period</p>
                    <p>30 Days</p>
                </h4>

                <h4>
                    <p>NFT Remaining</p>
                    <p>{totalNft} {name}</p>
                </h4>

                <h4>
                    <p>Staked NFTs</p>
                    <p>{totalStake} {name}</p>
                </h4>

                <CountdownWidget endTime={date[1]} startTime={date[0]} />
            </div>
        </CellCollections>
    );
}



