import {useCallback, useEffect, useState} from "react";
import {compose, flatten, map} from "ramda";
import * as R from "ramda";
import {MyPoolData, RootMyPoolData} from "../../../model/my_pool_data";
import BigNumber from "bignumber.js";
import {useWallet} from "use-wallet";
import {NftMateMap, PairsMap} from "../../collection/type";
import {PoolType} from "../../collection/pool/nft_pool_item";
import {useWeb3} from "../../../provider/web3_provider";

type useGetPoolMessages = {
    nftAddress: string
}
export const useGetPoolMessage = ({nftAddress}: useGetPoolMessages) => {
    const {account} = useWallet();
    const {block, contrcts} = useWeb3()

    const [date, setDate] = useState(['', ''])
    const [nftTotal, setTotal] = useState({})
    const [nftStake, setStake] = useState({})

    const getMessage = useCallback(async () => {
        let startTime = await contrcts.Stake.methods['startTime']().call()
        let endTime = await contrcts.Stake.methods['endTime']().call()
        setDate([startTime, endTime])
        let nftTotal:any = {}
        let nftStake:any = {}
        for (const nftTotalElement of contrcts.StakeNfts) {
           let amount =  await contrcts.Stake.methods['nftTotal'](nftTotalElement).call()
            nftTotal[nftTotalElement] = amount;
            let stake =  await contrcts.Stake.methods['nftStake'](nftTotalElement).call()
            nftStake[nftTotalElement] = stake;
        }

        setTotal(nftTotal)
        setStake(nftStake)

    }, [contrcts])
    useEffect(() => {
        const timer = setTimeout(() => {
            getMessage()
        }, 300)
        return () => clearTimeout(timer)
    }, [account, block])

    return {nftTotal, nftStake, date}
}
