import React, {useMemo} from "react";
import styled from "styled-components";

type ScheduleProps = {
    amount: string,
    stake: string,
    isShowSchedule?: boolean
}
export const ScheduleWidget: React.FC<ScheduleProps> = ({amount, stake, isShowSchedule = false}) => {
    const schedule = useMemo(() => {
        if (amount === '---') {
            return 20
        } else {
            return (parseInt(stake) / parseInt(amount)) * 100
        }
    }, [amount, stake])
    return (
        <ScheduleStyle>
            {
                isShowSchedule && <h3 className="title">{schedule}%</h3>
            }
            <div className="inner">
                <span/>
                <div>
                    <h5 style={{
                        width: schedule + "%"
                    }}/>
                </div>
                <span/>
            </div>
        </ScheduleStyle>
    )
}

const ScheduleStyle = styled.div`
  padding: 50px 0;

  .title {
    text-align: center;
    margin-bottom: 10px;
    color: white;
    font-size: 18px;
  }

  .inner {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;

    div {
      flex: 1;
      height: 2px;
      background-color: #235848;

      h5 {
        height: 2px;
        background-color: #00FFB3;
      }
    }

    span {
      width: 11px;
      height: 11px;
      background: #00FFB3;
      display: block;
      border-radius: 100%;
    }
  }



`
