import React, {useContext, useState} from "react";
import {ModalContext} from "../../../provider/model_provider";
import styled from "styled-components";
import {SelectNftWidget} from "../select_nft_wigdte";
import {Grid} from "@material-ui/core";
import {MoralisData} from "../../../model/moralis_data";
import Roy from "../../../assets/images/question-mark.svg";
import {NftCardItem} from "../../inventory/widget/nft_card_item";
import {useGetNfts} from "../hook/get_nfts";
import {useWeb3} from "../../../provider/web3_provider";
import {NftItem} from "../type";
import {useDeposit, useWithdraw} from "../../../server/stake";
import {useSendModal} from "../../../widget/modal/send_modal";
import {LoadingBtn} from "../../../compones/loading_btn";

export const WithdrawWidget: React.FC = () => {
    const {contrcts} = useWeb3()
    let nftAddress = contrcts.StakeNfts[0]
    const {nftMeta} = useGetNfts(nftAddress)
    const handleWithdraw = useWithdraw()
    const {onSend, status,} = useSendModal(handleWithdraw)

    return (
        <DepositStyle>
            <div className="nft_box">
                <h3 className="total_nfts">Total Deposited NFTs</h3>

                <div className="nfts">
                    <Grid className={'nft_wap'} container spacing={2}>
                        {
                            (Object.values(nftMeta) || []).map((ev: NftItem) => {

                                return <Grid item key={`${ev.symbol}_${ev.nftId}`}>
                                    <NftCardItem
                                        data={{
                                            id: nftAddress,
                                            imgUrl: ev.imgUrl,
                                            nftId: ev.nftId,
                                            symbol: ev.symbol,
                                            isSelect: ev.isSelect,
                                        }}
                                        onCb={(select: boolean) => {
                                            ev.isSelect = select;
                                        }}
                                    />
                                </Grid>
                            })
                        }
                    </Grid>
                </div>
            </div>

            <LoadingBtn isLoading={status} onClick={() => {
                onSend({
                    nftCtx: nftAddress,
                })
            }}>
                Withdraw
            </LoadingBtn>
        </DepositStyle>
    )
}


