import {useWeb3} from "../provider/web3_provider";
import {useWallet} from "use-wallet";
import {useSendFn} from "../utils/web3_send";
import {useCallback} from "react";


export let a = '';


export const useDeposit = () => {
    const {contrcts} = useWeb3();
    const {account} = useWallet()
    const handleSendFn = useSendFn(contrcts.Stake_A);
    const handleCreatePairETH = useCallback(async ({
                                                       nftCtx,
                                                       nftIds
                                                   }: { nftCtx: string, nftIds: string[] }): Promise<any> => {
        let method = contrcts.Stake.methods['deposit'](nftCtx, nftIds)
        return await handleSendFn(method);
    }, [contrcts, account]);
    return handleCreatePairETH;
}

export const useWithdraw = () => {
    const {contrcts} = useWeb3();
    const {account} = useWallet()
    const handleSendFn = useSendFn(contrcts.Stake_A);
    const handleCreatePairETH = useCallback(async ({nftCtx}: { nftCtx: string }): Promise<any> => {
        let method = contrcts.Stake.methods['withdraw'](nftCtx, account)
        return await handleSendFn(method);
    }, [contrcts, account]);
    return handleCreatePairETH;
}
