import Roy from "../../assets/images/question-mark.svg";
import cloneImg from "../../assets/images/colse_img.png"
import {ModalProps} from "../../provider/model_provider";
import {useStore} from "effector-react";
import {$getMoralisFileData} from "../inventory/store";
import * as R from "ramda";
import {NftCardItem, NftSellCardItem} from "../inventory/widget/nft_card_item";
import {LoadingWidget} from "../../widget/base/loading_widget";
import {FullNaData} from "../../widget/base/na_data";
import {getMoralisFileData} from "../inventory/store/effects";
import {ApproveButton} from "../../widget/base/approve_button";
import {LoadingBtn} from "../../compones/loading_btn";
import {PoolType} from "../collection/pool/nft_pool_item";
import {useWeb3} from "../../provider/web3_provider";
import {Contract} from "web3-eth-contract";
import erc721 from "../../web3/abi/erc721";
import {disposeCoordSysRecordIfNeeded} from "echarts/types/src/component/dataZoom/roams";


type SellProps = {
    addNfts: Function,
    ids: string[]
} & ModalProps

export const SelectNftWidget: React.FC<SellProps> = ({onDismiss, addNfts, ids}) => {


    const data = useStore($getMoralisFileData)
    const loading = useStore(getMoralisFileData.pending)
    const [result, setResult] = useState(data?.result || [])
    const {contrcts, web3} = useWeb3()
    useEffect(() => {
        if (!result?.length) {
            setResult(data?.result)
        }
    }, [data])

    const [key, setKey] = useState("")

    let group: any = useMemo(() => {
        if (!result?.length) {
            return {}
        }
        let group = R.compose(
            R.groupBy((student: MoralisData) => {
                return student.token_address;
            }),
        )(result)
        setKey(Object.keys(group)[0])
        return group;
    }, [])


    const contract: Contract = useMemo(() => {
        return new web3.eth.Contract(erc721, key)
    }, [key])


    if (loading || !data.result) {
        return <LoadingWidget/>;
    }

    if (!data?.result.length) {
        return <FullNaData>empty</FullNaData>;
    }

    return (
        <SellStyle>
            <div className="sell_left">
                <h4>Collections</h4>
                {
                    (Object.keys(group) || []).map((ev: string) => {
                        return <h4 className="table" onClick={() => {
                            setKey(ev);
                        }}>
                            <p>{group[ev][0].symbol}</p>
                            <span>{group[ev].length}</span>
                        </h4>
                    })
                }

            </div>
            <div className="right">
                <h3 className="title">You are Listing {
                    onDismiss && <img onClick={() => onDismiss()} src={cloneImg} alt=""/>
                }</h3>
                <div className="box">
                    <Grid container spacing={2}>
                        {
                            (group[key] || []).map((ev: MoralisData, index: number) => {
                                let metadata = JSON.parse(ev.metadata)
                                if (!metadata) {
                                    metadata = {
                                        name: ev.name,
                                        image: Roy
                                    }
                                }

                                return <Grid item key={`${ev.name}_${ev.token_id}`}>
                                    <NftSellCardItem
                                        data={{
                                            id: ev.token_address,
                                            imgUrl: metadata.image || metadata.image_url,
                                            nftId: ev.token_id,
                                            symbol: metadata.name || ev.name,
                                            isSelect: ev.isChecked,
                                        }}
                                        onCb={(select: boolean) => {
                                            ev['isChecked'] = select;
                                        }}
                                    />
                                </Grid>
                            })
                        }
                    </Grid>
                </div>
                <div style={{height: "20px"}}/>


                <ApproveButton
                    contractAddress={key}
                    contract={contract}
                    spender={contrcts.Stake_A}
                >
                    <div className="select_btn" onClick={() => {
                        let isSelect = result.filter((ev: any) => ev.isChecked)
                        addNfts(isSelect)
                        onDismiss && onDismiss()
                    }}>
                        Select
                    </div>
                </ApproveButton>
                {/*<div className="select_btn" onClick={() => {*/}
                {/*    let isSelect = result.filter((ev: any) => ev.isChecked)*/}
                {/*    addNfts(isSelect)*/}
                {/*    onDismiss && onDismiss()*/}
                {/*}}>*/}
                {/*    Select*/}
                {/*</div>*/}
            </div>
        </SellStyle>
    );
}



