import React, {useMemo} from "react";
import styled from "styled-components";
import {reduce} from "ramda";
import BigNumber from "bignumber.js";
import {ScheduleWidget} from "../../main/widget/schedule_widget";
import {useWeb3} from "../../../provider/web3_provider";
import ShearAddress from "../../../assets/images/shear_address.png"
import {UrlHaxFormat} from "../../../utils/url";
import {useWallet} from "use-wallet";

type MessageProps = {
    nftStake: {}, nftTotal: {}, date: string[]
}
export const MessageWidget: React.FC<MessageProps> = ({nftTotal, nftStake, date}) => {

    const {contrcts} = useWeb3()
    const {chainId} = useWallet()
    const totalStake = useMemo(() => {
        let listData: string[] = Object.values(nftStake);
        if (listData.length === 0) return "---"
        return reduce((acc, per: string) => acc.plus(per), new BigNumber(0))(listData).toFixed(0)
    }, [nftStake])

    const totalNft = useMemo(() => {
        let listData: string[] = Object.values(nftTotal);
        if (listData.length === 0) return "---"
        return reduce((acc, per: string) => acc.plus(per), new BigNumber(0))(listData).toFixed(0)
    }, [nftTotal])

    let [startTime, endTime] = date
    return (
        <MessageStyle>
            <div className={'item_text'}>
                <h3>Launch Mode</h3>
                <h4>STAKE TO MINT</h4>
            </div>
            <div style={{height: "45px"}}/>
            <div className={'item_text'}>
                <h3>Staking Period</h3>
                <h4>30 Days</h4>
            </div>
            <div style={{height: "45px"}}/>

            <div className="amount">
                <div className={'item_text'}>
                    <h3>Amount For Mint</h3>
                    <h4>{totalNft}</h4>
                </div>
                <div className="line">
                    <p/>
                </div>
                <div className={'item_text'}>
                    <h3>NFT Remining</h3>
                    <h4>{totalStake}</h4>
                </div>
            </div>

            <div className="message_box">
                <ScheduleWidget isShowSchedule={true} amount={totalNft} stake={totalStake}/>

            </div>

            <div className="amount">
                <div className={'item_text_mi'}>
                    <h3>START TIME</h3>
                    <h4>{startTime ? new Date(parseInt(startTime) * 1000).toISOString() : '--'}</h4>
                </div>
                <div className="line">
                    <p/>
                </div>
                <div className={'item_text_mi'}>
                    <h3>END TIME</h3>
                    <h4>{endTime ? new Date(parseInt(endTime) * 1000).toISOString() : "--"}</h4>
                </div>
            </div>

            <div style={{height: "24px"}}/>
            <div className={'item_text_mi'}>
                <h3>Staking Contract</h3>
                <h4
                    onClick={(ev) => {
                        window.open(UrlHaxFormat(contrcts.Stake_A, 'address', chainId), "_block")
                    }}
                    style={{textDecorationLine: "underline"}}
                >
                    {contrcts.Stake_A} <img src={ShearAddress} alt=""/>
                </h4>
            </div>

        </MessageStyle>
    )
}


const MessageStyle = styled.div`

  .line_box {
    padding: 50px 0;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;

    div {
      flex: 1;
      height: 2px;
      background-color: #235848;
    }

    span {
      width: 11px;
      height: 11px;
      background: #00FFB3;
      display: block;
      border-radius: 100%;
    }
  }


  .amount {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .line {
      //width: 100px;
      margin-right: 40px;

      p {
        height: 30px;
        width: 3px;
        background-color: #64616C;
      }
    }

    .item_text {
      flex: 1;
    }
  }

  .item_text_mi {
    width: 48%;

    h3 {
      font-family: 'ZX';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 12px;
      display: flex;
      align-items: center;
      letter-spacing: -0.14em;
      color: #848484;
    }

    h4 {
      margin-top: 5px;
      font-family: 'ZX';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      display: flex;
      align-items: center;
      letter-spacing: -0.14em;
      color: #FFFFFF;


      img {
        display: block;
        cursor: pointer;
        margin-left: 10px;
        width: 10px;
        height: 12px;
      }
    }
  }

  .item_text {
    width: 48%;

    h3 {
      font-family: 'ZX';
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 12px;
      display: flex;
      align-items: center;
      letter-spacing: -0.14em;
      color: #848484;
    }

    h4 {
      margin-top: 5px;
      font-family: 'ZX';
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 18px;
      display: flex;
      align-items: center;
      letter-spacing: -0.14em;
      color: #FFFFFF;
    }
  }

  width: 42%;
  display: block;
  background: #191B1F;
  padding: 40px 30px;
  border: 1px solid #64616C;
  border-radius: 20px;
  box-sizing: border-box;
`
